import React from 'react'
import athleteService from '../services/athletes'

const WorkoutList = ({name, workouts, athleteId, change}) => {
  const workoutListStyle = {
    textAlign: 'center',
    fontFamily: 'Trebuchet MS',
    borderCollapse: 'collapse',
    borderBottom: '3px solid #ddd',
    width: '100%'
  }

  const removeWorkout = (athleteId, workoutId, type) => {
    if (window.confirm(`Delete ${type} workout?`)) {
      
      athleteService
        .removeWorkout(athleteId, workoutId)
    }
  }

  return (
    <div style={workoutListStyle}>
    <table>
      <tbody>
        <tr>
          <th style={{paddingTop: '12px', paddingBottom: '12px', textAlign: 'center', backgroundColor: '#4CAF50', color: 'white' }}>
            {name}
          </th>
        </tr>
        {workouts.map(workout => {
          return (
            <tr key={workout._id}>
              <td style={{textAlign: 'left'}}>{new Date(workout.date).toDateString()}</td>
              <td>{workout.type}</td>
              <td>{workout.note}</td>
              <td><button onClick={() => removeWorkout(athleteId, workout._id, workout.type)}>Delete Workout</button></td>
            </tr>
            )
          })}
      </tbody>
    </table>
  </div>
  )
}

export default WorkoutList